/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { Options, Vue } from "vue-class-component";
import HomeComponent from "@/components/Home/index.vue";
import AdminLayout from "@/layouts/Admin/index.vue";
//import HelloWorld from "./components/HelloWorld.vue"
/*eslint-disable */
import Drawflow from "drawflow"
import BlocksMenu from "@/components/BlocksMenu/index.vue"
/*eslint-enable */

@Options({
  components: {
    HomeComponent,
    AdminLayout,
    BlocksMenu,
  },
})
export default class Home extends Vue {
  editor: any;
  mobile_item_selec: any = null;
  mobile_last_move: any = null;
  transform = "";
  open = false;

  toggle() {
    this.open = !this.open;
  }

  mounted() {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const vm = this;
    const id = document.getElementById("drawflow");
    // @ts-expect-error test
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    this.editor = new Drawflow(id!, Vue);
    this.editor.reroute = true;
    this.editor.start();

    // Events!
    this.editor.on("nodeCreated", function (id: string) {
      console.log("Node created " + id);
    });

    this.editor.on("nodeRemoved", function (id: string) {
      console.log("Node removed " + id);
    });

    this.editor.on("nodeSelected", function (id: string) {
      vm.toggle();
    });

    this.editor.on("moduleCreated", function (name: string) {
      console.log("Module Created " + name);
    });

    this.editor.on("moduleChanged", function (name: string) {
      console.log("Module Changed " + name);
    });

    this.editor.on("connectionCreated", function (connection: any) {
      console.log("Connection created");
      console.log(connection);
    });

    this.editor.on("connectionRemoved", function (connection: any) {
      console.log("Connection removed");
      console.log(connection);
    });

    this.editor.on("mouseMove", function (position: any) {
      console.log("Position mouse x:" + position.x + " y:" + position.y);
    });

    this.editor.on("nodeMoved", function (id: any) {
      console.log("Node moved " + id);
    });

    this.editor.on("zoom", function (zoom: any) {
      console.log("Zoom level " + zoom);
    });

    this.editor.on("translate", function (position: any) {
      console.log("Translate x:" + position.x + " y:" + position.y);
    });

    this.editor.on("addReroute", function (id: string) {
      console.log("Reroute added " + id);
    });

    this.editor.on("removeReroute", function (id: string) {
      console.log("Reroute removed " + id);
    });

    /* DRAG EVENT */

    /* Mouse and Touch Actions */

    const elements = document.getElementsByClassName("drag-drawflow");
    for (let i = 0; i < elements.length; i++) {
      elements[i].addEventListener("touchend", this.drop, false);
      elements[i].addEventListener("touchmove", this.positionMobile, false);
      elements[i].addEventListener("touchstart", this.drag, false);
    }
  }

  positionMobile(ev: any) {
    this.mobile_last_move = ev;
  }

  allowDrop(ev: any) {
    ev.preventDefault();
  }

  drag(ev: any) {
    if (ev.type === "touchstart") {
      this.mobile_item_selec = ev.target
        .closest(".drag-drawflow")
        .getAttribute("data-node");
    } else {
      ev.dataTransfer.setData("node", ev.target.getAttribute("data-node"));
    }
  }

  drop(ev: any) {
    if (ev.type === "touchend") {
      const x = this.mobile_last_move!.touches![0].clientX;
      const y = this.mobile_last_move!.touches![0].clientY;
      const elFromPoint = document.elementFromPoint(x, y);
      const parentdrawflow = elFromPoint!.closest("#drawflow");
      if (parentdrawflow != null) {
        this.addNodeToDrawFlow(
          this.mobile_item_selec,
          JSON.parse(this.mobile_item_selec),
          x,
          y
        );
      }
      this.mobile_item_selec = "";
    } else {
      ev.preventDefault();
      const id = ev.dataTransfer.getData("node");
      const data = ev.dataTransfer.getData("data");
      console.log("ANTES1", JSON.parse(data)), id;
      this.addNodeToDrawFlow(id, JSON.parse(data), ev.clientX, ev.clientY);
    }
  }

  addNodeToDrawFlow(id: string, data: any, pos_x: number, pos_y: number) {
    if (this.editor.editor_mode === "fixed") {
      return false;
    }
    pos_x =
      pos_x *
        (this.editor.precanvas.clientWidth /
          (this.editor.precanvas.clientWidth * this.editor.zoom)) -
      this.editor.precanvas.getBoundingClientRect().x *
        (this.editor.precanvas.clientWidth /
          (this.editor.precanvas.clientWidth * this.editor.zoom));
    pos_y =
      pos_y *
        (this.editor.precanvas.clientHeight /
          (this.editor.precanvas.clientHeight * this.editor.zoom)) -
      this.editor.precanvas.getBoundingClientRect().y *
        (this.editor.precanvas.clientHeight /
          (this.editor.precanvas.clientHeight * this.editor.zoom));
    console.log("DATAAAAA", data.id);
    switch (data.id) {
      case 1:
        // eslint-disable-next-line no-case-declarations
        const hf = `
        <div>
          <div class="title-box"><i class="${data.logo_class}"></i> ${data.name}</div>
        </div>
        `;
        this.editor.addNode("hf", 1, 1, pos_x, pos_y, "hf", {}, hf);
        break;
      case 4:
        // eslint-disable-next-line no-case-declarations
        const facebook = `
        <div>
          <div class="title-box"><i class="${data.logo_class}"></i> ${data.name}</div>
        </div>
        `;
        this.editor.addNode(
          "facebook",
          1,
          1,
          pos_x,
          pos_y,
          "facebook",
          {},
          facebook
        );
        break;
      case 5:
        // eslint-disable-next-line no-case-declarations
        const pepe = `
        <div>
          <div class="title-box"><i class="${data.logo_class}"></i> ${data.name}</div>
        </div>
        `;
        this.editor.addNode("pepe", 0, 1, pos_x, pos_y, "pepe", {}, pepe);
        break;
      default:
    }
  }

  showpopup(e: any) {
    e.target.closest(".drawflow-node").style.zIndex = "9999";
    e.target.children[0].style.display = "block";
    //document.getElementById("modalfix").style.display = "block";

    //e.target.children[0].style.transform = 'translate('+translate.x+'px, '+translate.y+'px)';
    this.transform = this.editor.precanvas.style.transform;
    this.editor.precanvas.style.transform = "";
    this.editor.precanvas.style.left = this.editor.canvas_x + "px";
    this.editor.precanvas.style.top = this.editor.canvas_y + "px";
    console.log(this.transform);

    //e.target.children[0].style.top  =  -editor.canvas_y - editor.container.offsetTop +'px';
    //e.target.children[0].style.left  =  -editor.canvas_x  - editor.container.offsetLeft +'px';
    this.editor.editor_mode = "fixed";
  }

  closemodal(e: any) {
    e.target.closest(".drawflow-node").style.zIndex = "2";
    e.target.parentElement.parentElement.style.display = "none";
    //document.getElementById("modalfix").style.display = "none";
    this.editor.precanvas.style.transform = this.transform;
    this.editor.precanvas.style.left = "0px";
    this.editor.precanvas.style.top = "0px";
    this.editor.editor_mode = "edit";
  }

  changeModule(event: any) {
    const all = document.querySelectorAll(".menu ul li");
    for (let i = 0; i < all.length; i++) {
      all[i].classList.remove("selected");
    }
    event.target.classList.add("selected");
  }
}
