import { Options, Vue } from "vue-class-component";
import MenuBlock from "@/components/MenuBlock/index.vue";
import MenuBlockPlaceHolder from "@/components/PlaceHolders/MenuBlockPlaceHolder/index.vue";
import "vue-skeletor/dist/vue-skeletor.css";

interface Filter {
  name: string;
  value: string;
}

@Options({
  name: "BlocksMenuComponent",
  components: {
    MenuBlock,
    MenuBlockPlaceHolder,
  },
})
export default class BlocksMenu extends Vue {
  items = [];
  filteredProperty = "block_type";
  query = "";
  activeFilters: Filter[] = [];
  isLoadingData = false;

  async created() {
    // TODO remove, just for demo purposes
    function delay(ms: number) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    }
    // POST request using fetch with async/await
    this.isLoadingData = true;
    const requestOptions = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Basic aW50ZWdyYXRpb246ZThMWHd3YXJ3MmU0VlN4",
      },
    };
    await delay(1000);
    const response = await fetch(
      "https://botoapp.io/api/blocks/",
      requestOptions
    );
    const data = await response.json();
    this.items = data;
    this.isLoadingData = false;
  }

  addFilter() {
    this.activeFilters.push({
      name: this.filteredProperty,
      value: this.query,
    });
    this.query = "";
  }

  removeFilter(idx: any) {
    this.activeFilters.splice(idx, 1);
  }
}
